.radarBlock {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.radar {
    position: relative;
    display: inline-block;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background: white;
    border: 5px solid #ec4411;
    animation: pulse 2s linear infinite;
}

.rightDirection {
    border-color: #1ddcc2;
}

.distance {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

@keyframes pulse {
    0% {
        border-width: 5px;
        opacity: 1;
    }
    10% {
        border-width: 15px;
        opacity: 0.8;
    }
    20% {
        border-width: 25px;
        opacity: 0.6;
    }
    30% {
        border-width: 35px;
        opacity: 0.8;
    }
    40% {
        border-width: 45px;
        opacity: 1;
    }
    50% {
        border-width: 55px;
        opacity: 0.8;
    }
    60% {
        border-width: 45px;
        opacity: 0.6;
    }
    70% {
        border-width: 35px;
        opacity: 0.8;
    }
    80% {
        border-width: 25px;
        opacity: 1;
    }
    90% {
        border-width: 15px;
        opacity: 0.8;
    }
    100% {
        border-width: 5px;
        opacity: 0.6;
    }
}